import { api_judge_user_access, api_user_access } from '@/api/account/account';
import { IAccount } from '@/api/account/account.i';
import { api_landing_page_random } from '@/api/common/common';
import { ILandingPageRandom } from '@/api/common/common.i';
import { api_template_subscription } from '@/api/mina_app/mina_app';
import { api_receive_share_card } from '@/api/share/share';
import Loading from '@/components/loading/loading';
import { ApiStore } from '@/store';
import { getToken, httpCheckLogin } from '@/utils/login';
import { debounce, getEntry, getStorage, throttle, isIos } from '@/utils/tools';
import { track } from '@/utils/track';
import WX from '@/utils/WX';
import { Image, ScrollView, Text, View, CustomWrapper } from '@tarojs/components';
import { AtModal } from 'taro-ui';

import { nextTick, useRouter } from '@tarojs/taro';
import { observer } from 'mobx-react';
import { useState, useEffect, useRef } from 'react';
import './activity.scss';
import BulletChat from './components/bullet-chat/bullet-chat';
import CardDialog from './components/card-dialog/card-dialog';
import UserNum from './components/user-num/user-num';
import PlayDirectory from './components/play-directory/play-directory';
import { IChannelDailyContentGet, IContentGet } from '@/api/content/content.i';
import { api_channel_daily_content_get } from '@/api/content/content';

type FomateDailyContent = IChannelDailyContentGet.FomateDailyContent;
const Activity = observer(() => {
  const [headImage, setHeadImage] = useState('');
  const [loading, setLoading] = useState(true); // 首屏图片加载中
  const [description, setDescription] = useState<string[]>([]);
  const [plan, setPlan] = useState<string[]>([]);
  const [currTab, setCurrTab] = useState(0); // 当前tab
  const [userNum, setUserNum] = useState<number | undefined>();
  const [showDialog, setShowDialog] = useState(false); // 是否弹出领取成功弹窗
  const [scrollTop, setScrollTop] = useState(''); // 每次切换tab时，需要置顶tab
  const [landingType, setLandingType] = useState('ordinary'); //落地页类型
  const [redEnvelope, setredEnvelope] = useState(true); //红包的状态
  const [dialogStatus, setDialogStatus] = useState(false);
  const [fomateDailyContent, setFomateDailyContent] = useState<FomateDailyContent[]>([]); //音频信息
  const subscribeList = useRef<string[]>([
    '9-P1Vuc8OOuhg8CBLIOFWfjd1FsC2aLXtGNO1sWEcG4',
    '8LumSmpu4cipHE31pXG6KPn5QHi8aG1WzVxj710o-Kg',
    'ofoXGPaa5O5rdKshDwwlaTvwoMol1CuVbSrw8_GswA8',
  ]); // 订阅列表
  const hasUserInfo = useRef(false); // 是否有用户权限
  const hasSubscribe = useRef(false); // 是否已经订阅过消息
  const isAccess = useRef(false); // 用户是否授权登录过
  const isShowDialog = useRef(false); // 是否弹出过领取的弹窗
  const router = useRouter();

  /** 初始化 */
  useEffect(() => {
    getChannelDaily();
    // 亲友卡
    if (router.params.user_type === 'presented') {
      getCoverImgs('friend');
    } else {
      getCoverImgs('common');
      //判断皮肤类型
      if (landingType === 'ordinary') {
      }
    }
    // getHasUserSubscribe();
    // 设置剩余名额
    const num = getStorage('activity_user_num') || 100;
    setUserNum(num);
  }, []);

  useEffect(() => {
    if (loading) return;
    // 登录完成后调用
    getToken(
      () => {
        // 用户存在登录态，就获取用户状态
        getHasUserInfo();
      },
      () => {
        // 拿不到登录态， 全新用户，并且是亲友卡，直接弹窗
        if (!isShowDialog.current && router.params.user_type === 'presented') {
          setShowDialog(true);
          setTimeout(() => setShowDialog(false), 5000);
          isShowDialog.current = true;
        }
      }
    );
  }, [loading]);

  const onTabChange = (curr: number) => {
    setCurrTab(curr);
    nextTick(() => {
      setScrollTop(`scrolltop${curr}`);
    });
  };

  const getChannelDaily = async () => {
    const remotChannel = await api_channel_daily_content_get({ channel_id: 1067901 });
    setFomateDailyContent([...remotChannel.fomate_daily_content]);
    console.log(remotChannel, '专栏信息');
  };
  /**
   * 获取封面信息
   * @params page_type 模板类型
   */
  const getCoverImgs = async (page_type: ILandingPageRandom.PageType) => {
    try {
      const res = await api_landing_page_random({ page_type });
      setHeadImage(res?.head_image_url ?? '');
      setDescription(res?.description ?? []);
      setPlan(res?.plan ?? []);
      console.log('res--->', res);
    } catch (error) {
      WX.Toast('获取封面信息失败');
      console.error('getCoverImgs error', error);
    }
  };

  /** 获取用户信息 */
  const getHasUserInfo = async () => {
    try {
      const res = await api_judge_user_access();
      console.log('res', res);
      isAccess.current = res?.is_access ?? false;
      if (res?.is_access) {
        track.setEvent('lh_activity_show');
        //写完打开
        await WX.Toast({ title: '你已是听书用户，无需领取哦~', duration: 4000 });
        // WX.switchTab('/pages/home/home');
        return;
      } else {
        ApiStore.user.setUserType('new');
        track.setEvent('lh_activity_show_newuser');
      }
      if (router.params.user_type === 'presented') {
        await getPresented();
      }
      hasUserInfo.current = true;
    } catch (error) {
      console.error('getUserInof error', error);
      // 全新用户
      if (error?.code === '0401') {
        console.log('全新用户');
        ApiStore.user.setUserType('new');
        track.setEvent('lh_activity_show_newuser');
      } else {
        return Promise.reject(error);
      }
    }
  };
  /** 获取用户订阅信息 */
  const getHasUserSubscribe = async () => {
    const subList = [
      '9-P1Vuc8OOuhg8CBLIOFWfjd1FsC2aLXtGNO1sWEcG4',
      '8LumSmpu4cipHE31pXG6KPn5QHi8aG1WzVxj710o-Kg',
      'ofoXGPaa5O5rdKshDwwlaTvwoMol1CuVbSrw8_GswA8',
    ] as const;
    type SubList = typeof subList[number];
    type SettingStatus = 'accept' | 'reject' | 'ban'; // 'accept'表示用户同意订阅这条消息，'reject'表示用户拒绝订阅这条消息，'ban'表示已被后台封禁
    type ItemSettings = Record<SubList, SettingStatus>;
    wx.getSetting({
      withSubscriptions: true,
      success(res) {
        console.log('getSetting', res);
        // 如果有itemSettings代表用户是点击了记住
        const itemSettings = res?.subscriptionsSetting?.itemSettings as ItemSettings;
        const nonSubList = subList.filter((sub) => {
          console.log('itemSettings', itemSettings);
          return itemSettings?.[sub] !== 'accept';
        });
        if (nonSubList.length > 0) {
          console.log('存在未订阅的消息', nonSubList);
          subscribeList.current = nonSubList;
          hasSubscribe.current = false;
        } else {
          hasSubscribe.current = true;
          console.log('消息已经订阅过', nonSubList);
        }
      },
    });
  };
  const goFree7Day = useRef(
    throttle(
      async () => {
        try {
          if (isAccess.current) return WX.Toast({ title: '你已是听书用户，无需领取哦~', duration: 4000 });
          console.log('goFree7Day', hasUserInfo.current, subscribeList.current);
          if (!hasUserInfo.current) {
            await toLogin(); // 先登录
            await getHasUserInfo(); // 重新走获取用户信息的逻辑
            return WX.Toast('请再次点击按钮~');
          }
          if (!hasSubscribe.current) {
            await toSubscribe(); // 再订阅
          }
          const source = router.params.user_type ?? (router.params.gift ? 'gift' : 'normal');
          console.log('source', source);
          track.setEvent('lh_activity_7d_ck', { source });
          // 亲友卡会自动加入课程，非亲友卡，手动调用加入课程
          if (source !== 'presented') {
            const res = await api_user_access({ source: 'normal' });
            // if (res) await WX.Toast('领取成功！'); // 去掉提示
          }
          // 跳转课程页
          WX.navigateTo('/pages/channel-plan/channel-plan');
        } catch (error) {
          console.error('goFree7Day', error);
        }
      },
      1500,
      { trailing: false }
    )
  ).current;

  /** 授权登录 */
  const toLogin = () => {
    return new Promise<IAccount.UserInfo>(async (resolve, reject) => {
      try {
        if (getStorage('account').id) return resolve(getStorage('account'));
        const e = await WX.getUserProfile();
        console.log('e--getUserProfile', e);
        wx.showLoading({ title: '登录中...' });
        // TODO 这里有时候不成功
        const res = await httpCheckLogin({ iv: e.iv, encryptedData: e.encryptedData });
        resolve(res.userinfo);
      } catch (error) {
        reject(error);
      } finally {
        wx.hideLoading();
      }
    });
  };

  /** 订阅消息 */
  const toSubscribe = async (): Promise<boolean> => {
    return new Promise((resolve) => {
      wx.requestSubscribeMessage({
        tmplIds: subscribeList.current,
        success(res) {
          console.log('requestSubscribeMessage res', res);
          const subList: string[] = []; // 已经订阅的列表
          const nonSubList: string[] = []; // 没有订阅的列表
          subscribeList.current.forEach((sub) => {
            if (res?.[sub] === 'accept') {
              subList.push(sub);
            } else {
              nonSubList.push(sub);
            }
          });
          console.log('已订阅成功', subList);
          console.log('未订阅成功', nonSubList);
          if (subList.length) {
            // 订阅消息发送
            api_template_subscription({ template_ids: subList });
          }
          // WX.Toast('请再次点击按钮~');
          hasSubscribe.current = true;
          resolve(true);
        },
        fail(error) {
          console.error('订阅失败', error);
          resolve(false);
          return error;
        },
        complete() {
          hasSubscribe.current = true; // 用户不管点击取消还是什么，都默认通过
        },
      });
    });
  };

  /** 领取亲友卡 */
  const getPresented = async () => {
    try {
      const { channel_id, sender_id } = router.params as unknown as { channel_id: number; sender_id: number };
      if (!channel_id || !sender_id) {
        console.error('领卡失败', router.params);
        WX.Toast('领取失败！缺少必要参数');
        return Promise.reject({ message: 'channel_id或者sender_id为空', params: { channel_id, sender_id } });
      }
      await api_receive_share_card({ channel_id, sender_id });
      // 如果弹过的，就不谈了
      if (!isShowDialog.current) {
        setShowDialog(true);
        setTimeout(() => {
          setShowDialog(false);
        }, 5000);
      }
    } catch (error) {
      console.error('领卡失败 error', error);
      WX.Toast('领卡失败！' + error?.message ?? '');
      return Promise.reject(error);
    }
  };
  //书单计划展开收进
  const audition = () => {
    let planType = dialogStatus;
    planType = !planType;
    setDialogStatus(planType);
  };
  //立刻加入操作
  const gojoinNow = () => {
    ApiStore.player.goPause();
    ApiStore.player.playingState = 'ended';
    // WX.switchTab('/pages/home/home');
    if (!isIos) {
      console.log('直接付款');
    } else {
      console.log('跳转页面');
    }
  };
  //开红包
  const openRed = () => {
    setredEnvelope(false);
    WX.Toast('领取成功！请尽快使用，以免失效');
  };
  return (
    <View className="activity-page">
      {loading && <Loading></Loading>}
      <ScrollView className="ap-scroll" scrollY scrollIntoView={scrollTop}>
        <View>
          <BulletChat></BulletChat>
          <View className="ap-bg-box">
            <Image
              id="header-bg"
              onLoad={() => setLoading(false)}
              onError={() => setLoading(false)}
              lazyLoad
              className="ap-img-item"
              src={headImage}
              mode="widthFix"></Image>
          </View>
          {/* 为了定位的两个点 */}
          <View id="scrolltop0"></View>
          <View id="scrolltop1"></View>
          <View className="tab-box">
            <View className={`tab-item ${currTab === 0 && 'active'}`} onClick={() => onTabChange(0)}>
              课程介绍
            </View>
            <View className="tab-line"></View>
            <View className={`tab-item ${currTab === 1 && 'active'}`} onClick={() => onTabChange(1)}>
              书单计划
            </View>
          </View>
          <View className="ap-tab-content">
            {currTab === 0 && (
              <View className="ap-img-box">
                {description.map((url, i) => (
                  <Image lazyLoad className="ap-img-item" src={url} key={url} mode="widthFix"></Image>
                ))}
              </View>
            )}
            {currTab === 1 && (
              <View className="ap-img-box">
                {plan.map((url, i) => (
                  <Image lazyLoad className="ap-img-item" src={url} key={url} mode="widthFix"></Image>
                ))}
              </View>
            )}
          </View>
        </View>
      </ScrollView>
      <CardDialog value={showDialog} setValue={setShowDialog}></CardDialog>
      <AtModal className="red-envelope" closeOnClickOverlay={false} isOpened={redEnvelope}>
        <Image
          className="red-envelope-bg"
          src="https://static-1253442168.file.myqcloud.com/xupload/20210923_activity_red_bg_AtXASzJ.png"></Image>
        <Image
          onClick={openRed}
          className="red-envelope-btn"
          src="https://static-1253442168.file.myqcloud.com/xupload/20210923_activity_open_bg_LtCrhs2.png"></Image>
      </AtModal>
      <CustomWrapper>
        <PlayDirectory
          fomateDailyContent={fomateDailyContent}
          dialogStatus={dialogStatus}
          setDialogStatus={setDialogStatus}></PlayDirectory>
      </CustomWrapper>

      <View className="apb-user-box">
        <View className="apb-user-text">{userNum !== undefined && <UserNum num={userNum}></UserNum>}</View>
        <Image className="apb-user-bg" src={require('../../assets/images/activity_bottom_bg.png')}></Image>
      </View>
      {landingType !== 'ordinary' ? (
        <View className="ap-bottom-box">
          <View className="ap-bottom-ordinary-box" onClick={goFree7Day}>
            {/* <Button className="apb-promission-btn" openType=""></Button> */}
            <Text>去免费听7天</Text>
            <Image className="apb-btn-bg" src={require('../../assets/images/activity_buy_bg.png')}></Image>
            <Image className="apb-btn-hand" mode="widthFix" src={require('../../assets/images/activity_hand.png')}></Image>
          </View>
        </View>
      ) : (
        <View className="ap-bottom-box">
          <View className="apb-btn">
            {/* <Button className="apb-promission-btn" openType=""></Button> */}
            <View className="apb-audition-box">
              <Text className="apb-audition-deposit">
                押金<Text>￥{redEnvelope === false ? 99 : 199}</Text>
              </Text>
              <Text className="apb-audition-remind">学完全额退款</Text>
            </View>
            <View className="apb-btn-audition" onClick={audition}>
              <Text className="icon icon-shitingliebiao"></Text>
              <Text>试听</Text>
              <Image className="apb-btn-bg" src={require('../../assets/images/activity_audition_bg.png')}></Image>
            </View>
            <View className="apb-btn-box" onClick={gojoinNow}>
              <Text>立刻加入</Text>
              <Image className="apb-btn-bg" src={require('../../assets/images/activity_buy_bgt.png')}></Image>
              <Image className="apb-btn-hand" mode="widthFix" src={require('../../assets/images/activity_hand.png')}></Image>
            </View>
          </View>
        </View>
      )}
    </View>
  );
});
export default Activity;
