import Dialog from '@/components/dialog/dialog';
import { ApiStore } from '@/store';
import { CSSModule } from '@/utils/css-module';
import WX from '@/utils/WX';
import { Block, Button, Image, Text, View } from '@tarojs/components';
import { useState } from 'react';
import style from './card-dialog.module.scss';

interface ICardDialog {
  value: boolean;
  setValue: Function;
}

const CardDialog = CSSModule((props: ICardDialog) => {
  const { value, setValue } = props;
  console.log('CardDialog');

  return (
    <Dialog value={value} setValue={setValue} backgroundColor="transparent" showClose>
      <View styleName="card-dialog-wrapper">
        <Image styleName="light-icon" src="https://static-1253442168.file.myqcloud.com/xupload/20210918_guang.png"></Image>
        <View styleName="cd-card">
          <Image
            styleName="medal-icon"
            src="https://static-1253442168.file.myqcloud.com/xupload/20210831_xunzhang.png"
            mode="widthFix"></Image>
          <View styleName="card-title">领取成功</View>
          <Image
            styleName="card-bg"
            mode="heightFix"
            src="https://static-1253442168.file.myqcloud.com/xupload/20210909_card_NHe970J.png"></Image>
          <View styleName="card-desc">恭喜你！获得一张梨花听书7天畅听卡 你将可免费听7天的课程！在这7天，请轻松愉快的听书吧～</View>
        </View>
      </View>
    </Dialog>
  );
}, style);
export default CardDialog;
