import { Image, Text, View } from '@tarojs/components';
import style from './bullet-chat.module.scss';
import { useEffect, useMemo, useState } from 'react';
import { api_get_robot_accounts } from '@/api/robot/robot';
import { IGetRobotAccounts } from '@/api/robot/robot.i';
import { AtNoticebar } from 'taro-ui';
import { getPicture } from '@/utils/tools';
import { CSSModule } from '@/utils/css-module';

interface IBulletChat {}
const BulletChat = CSSModule((props: IBulletChat) => {
  const [robotList, setRobotList] = useState<IGetRobotAccounts.data>([]);

  useEffect(() => {
    getRobotList();
  }, []);

  const getRobotList = async () => {
    try {
      // 3s后再去请求跑马灯列表 优化并行请求数据，避免浏览器max fetch 阻塞其他业务请求
      await new Promise((res) => setTimeout(() => res(1), 3000));
      const res = await api_get_robot_accounts({ limit: 20 });
      console.log('res', res);
      if (res?.length) {
        setRobotList(res);
      }
    } catch (error) {
      console.error('getRobotList', error);
    }
  };
  return useMemo(() => {
    return (
      <View styleName="bullet-chat">
        {robotList.length > 0 && (
          <AtNoticebar marquee speed={40}>
            <View styleName="bc-marquee">
              {robotList.map((user) => (
                <View styleName="user-info" key={user.id}>
                  <Image styleName="user-avatar" lazyLoad src={getPicture(user.avatar_url, 26)} mode="aspectFill"></Image>
                  <View styleName="user-name">
                    <Text styleName="user-name-text">{user.nickname}</Text>刚刚加入成功
                  </View>
                </View>
              ))}
            </View>
          </AtNoticebar>
        )}
      </View>
    );
  }, [robotList]);
}, style);
export default BulletChat;
