import { getStorage, setStorage } from '@/utils/tools';
import { Text } from '@tarojs/components';
import { useEffect, useMemo, useState } from 'react';

interface IUserNum {
  num: number;
}
const UserNum = (props: IUserNum) => {
  const [num, setNum] = useState(props.num || 100);

  useEffect(() => {
    if (props.num <= 20) return; // 如果小于20，则不变
    //数字每秒随机减1-4 当数字减到20以下时 不再减
    const timer = setInterval(() => {
      const randomNum = Math.ceil(Math.random() * 4);
      setNum((old) => {
        if (old < 20) {
          clearInterval(timer);
          return old;
        }
        return old - randomNum;
      });
    }, 4000);

    return () => {
      clearInterval(timer);
    };
  }, []);

  useEffect(() => {
    setStorage('activity_user_num', num);
  }, [num]);

  return useMemo(
    () => (
      <>
        限时福利仅剩
        <Text>{num}</Text>
        个名额
      </>
    ),
    [num]
  );
};
export default UserNum;
