import { IChannelDailyContentGet } from '@/api/content/content.i';
import BottomDialog from '@/components/bottom-dialog/bottom-dialog';
import { ApiStore } from '@/store';
import { Block, ScrollView, Text, View } from '@tarojs/components';
import Taro, { nextTick } from '@tarojs/taro';
import { observer } from 'mobx-react';
import { useEffect, useMemo, useState, useRef } from 'react';
import './play-directory.scss';
import { IWeekLectureInfo } from '@/api/week_exam/week_exam.i';
import { PlayerStatus } from '@/store/player';

type FomateDailyContent = IChannelDailyContentGet.FomateDailyContent;
interface IPlayDirectory {
  dialogStatus: boolean;
  setDialogStatus: (param: boolean) => void;
  fomateDailyContent: FomateDailyContent[];
}
export type LecturesProps = {
  lectureList: IWeekLectureInfo.WeekLectureInfoList[];
  bookContent: IWeekLectureInfo.BookContent;
};
const icons = {
  loading: 'icon-zhoucejiazai1x',
  pause: 'icon-zhoucebofang1x',
  play: 'icon-zhoucezanting1x',
};

const PlayDirectory = observer((props: IPlayDirectory) => {
  const { dialogStatus, setDialogStatus, fomateDailyContent } = props;
  const [scrollHeight, setScrollHeight] = useState('0px');
  const [lectures, setLectures] = useState<IChannelDailyContentGet.FomateDailyContent[]>([]);
  const playStatus = ApiStore.player.getPlayInfo.status;
  const bookId = useRef(-1); //当前播放书籍的id

  const Header = useMemo(() => {
    return (
      <View className="directory-bg">
        <View className="directory-header flex">
          <View className="header-wrapper">
            <Text className="header-title">书单计划</Text>
          </View>
          <View onClick={() => setDialogStatus(false)}>
            <Text className="icon icon-guanbibeifen"></Text>
            <Text className="close-text">关闭</Text>
          </View>
        </View>
      </View>
    );
  }, [dialogStatus]);

  useEffect(() => {
    fomateDailyContent.forEach((item) => {
      item.content.forEach((book) => {
        book.playStatus = 'pause';
      });
    });
    setLectures([...fomateDailyContent]);
    // getLectureContent();
  }, [fomateDailyContent]);

  useEffect(() => {
    getScrollHeight();
  }, []);

  useEffect(() => {
    setPlayStatus(playStatus);
  }, [playStatus]);

  //清除状态
  const setPlayStatus = (status: PlayerStatus) => {
    lectures.forEach((item) => {
      item.content.forEach((book, index) => {
        if (book.object_id === bookId.current) {
          book.playStatus = status;
        } else {
          book.playStatus = 'pause';
        }
      });
    });

    setLectures([...lectures]);
  };

  /** 设置滚动盒子高度 */
  const getScrollHeight = () => {
    //沙雕处理方法 获取不到动态高度 就按屏幕高度写死
    const height = Math.floor((wx?.platform?.windowHeight ?? 0) * 0.55) + 'px';
    console.log('height', height, wx?.platform?.windowHeight, wx?.platform?.windowHeight ?? 0 * 0.55);
    setScrollHeight(height);
  };

  /**
   * @description: 点击播放暂停
   * @param {*} e
   * @return {*}
   */
  const bindAudio = (item, index, sub) => {
    playLecture(item, index, sub);
  };
  /**
   * 音频播放
   */
  const playLecture = (item, index, sub) => {
    console.log(item, '当前数据');
    const { author, anchor, unread, unlock, object_type, book_id, title, object_id, channel_id } = item;
    console.log(item);
    lectures[index].content[sub].playStatus = 'loading';
    bookId.current = item.object_id;
    setLectures([...lectures]);
    const { length, audio } = item.object_info;
    ApiStore.player.setPlayInfo({
      anchor,
      author,
      unlock,
      unread,
      object_type,
      book_id,
      title,
      object_id,
      channel_id,
      duration: length,
      src: audio,
      status: 'pause',
      cover_url: '',
    });
    ApiStore.player.goPlay();
  };
  return (
    <Block>
      {/*  测滑抽屉目录组件  */}
      <BottomDialog onClose={() => setDialogStatus(false)} value={dialogStatus}>
        {Header}
        <ScrollView scrollY style={'height: ' + scrollHeight} scrollWithAnimation={false}>
          <View className="panel-directory-wrapper">
            {lectures.map((items, index) => {
              return (
                <Block key={index}>
                  {items.week_num === 0 ? (
                    <View className="lecture-content flex">
                      <View className="items-title">先行课</View>
                    </View>
                  ) : (
                    <View className="lecture-content flex">
                      <View className="items-title">{'第' + items.week_num + '周'}</View>
                    </View>
                  )}
                  {items.content.map((book, sub) => {
                    return (
                      <View className="lecture-content flex" key={book.id} onClick={() => bindAudio(book, index, sub)}>
                        <View className="lecture-subtitle playing locked">{book.title}</View>
                        <Text className={`icon ${icons[book.playStatus]} play-icon`} style={{ color: '#9B6ADA' }}></Text>
                      </View>
                    );
                  })}
                </Block>
              );
            })}
          </View>
        </ScrollView>
      </BottomDialog>
    </Block>
  );
});

export default PlayDirectory;
